import { mapState } from 'vuex';
import { getUserById, getShopStatusByUserId } from '@/request/API/index.js';
export default {
	data() {
		return {
			shopStatus: -1,
			imgURl: '',
			imglist: '', //用户头像
			nickName: '', //用户昵称
			spec_name: '' //用户签名
		};
	},
	onShow() {
		this.imgURl = imgURl;
		this.getUserById();
		this.getShopStatusByUserId(this.userId);
	},
	computed: {
		...mapState(['userId'])
	},
	methods: {
		//获取用户入住状态
		getShopStatusByUserId(userId) {
			getShopStatusByUserId(userId).then(res => {
				if (res.data.code == 0) {
					this.shopStatus = res.data.data.shopStatus;
				}
			});
		},
		ruzhu() {
			if (this.shopStatus == 0) {
				uni.showToast({
					title: '入住信息审核中',
					icon: 'none',
					duration: 1000
				});
				return;
			}
			if (this.shopStatus == 1) {
				uni.showToast({
					title: '已经入驻成功',
					icon: 'none',
					duration: 1000
				});
				return;
			}
			if (this.shopStatus == 2) {
				uni.showModal({
					title: '',
					content: '入驻失败是否重新入驻',
					success: res => {
						if (res.confirm) {
							uni.navigateTo({
								url: '/pages/merchant/merchant'
							});
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
				return;
			}
			uni.navigateTo({
				url: '/pages/merchant/merchant'
			});
		},
		//获取用户信息
		getUserById() {
			getUserById(this.userId).then(res => {
				this.imglist = res.data.data.userPhoto;
				this.spec_name = res.data.data.specName;
				this.nickName = res.data.data.nickName;
			});
		},
		gotoUserMsg() {
			uni.navigateTo({
				url: '/pages/userMsg/userMsg'
			});
		}
	}
};
